import React from 'react';
import image from '../../../../assets/img/reactjs.png';

const Footer = () => (
    <div>
        <div className='footer_main'>
            <p> This page created with React JS</p>
            <img src={image} alt='logo'/>
        </div>
    </div>

);

export default Footer;




